import React, { Component } from "react";

class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      profile: {}
    };
  }
  componentWillMount() {
    if (this.props.auth.isAuthenticated()) {
      this.setState({ profile: {} });
      const { userProfile, getProfile } = this.props.auth;
      if (!userProfile) {
        getProfile((err, profile) => {
          this.setState({ profile });
        });
      } else {
        this.setState({ profile: userProfile });
      }
    }
  }
  goTo(route) {
    this.props.history.push(`/${route}`);
  }

  login() {
    this.props.auth.login();
  }

  logout() {
    this.props.auth.logout();
  }

  render() {
    return (
      <div>
        <nav className="navbar navbar-inverse">
          <div className="container-fluid">
            <div className="navbar-header">
              <a className="navbar-brand">
                <img
                  src="https://cdn.worldvectorlogo.com/logos/phoenix-36.svg"
                  width="30"
                  height="30"
                  alt="Premier League"
                />
                Premier League
              </a>
            </div>
            {this.props.auth.isAuthenticated() ? (
              <div className="profile-logo">
                <a>
                  <span>
                    {this.state.profile ? (
                      <img width="22px" src={this.state.profile.picture} />
                    ) : null}
                  </span>
                  <span style={{ marginLeft: "4px" }}>
                    {this.state.profile ? this.state.profile.name : null}
                  </span>
                </a>
              </div>
            ) : (
              ""
            )}

            <ul className="navbar-nav">
              <li className="nav-item">
                <div className="input-group">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Search"
                  />
                  <div className="input-group-append">
                    <button className="btn btn-success" type="submit">
                      Go
                    </button>
                  </div>
                </div>
              </li>
              <li className="nav-item" style={{ display: "flex" }}>
                {!this.props.auth.isAuthenticated() ? (
                  <a
                    className="nav-link active"
                    onClick={() => {
                      this.login();
                    }}
                  >
                    <span className="red">Login&nbsp;&nbsp;</span>
                  </a>
                ) : (
                  <a
                    className="nav-link active"
                    onClick={() => {
                      this.logout();
                    }}
                  >
                    <span className="green">Logout</span>
                  </a>
                )}
              </li>
            </ul>
          </div>
        </nav>
      </div>
    );
  }
}

export default index;
